import { MyPostCard } from '@/components/my-post-card';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { getPostsByAuthorId } from '@/lib/data';
import { getCurrentUser } from '@/lib/session';
import { PlusCircle, Info, CheckCircle } from 'lucide-react';
import Link from 'next/link';
import { redirect } from 'next/navigation';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';

export default async function MyPostsPage({
  searchParams,
}: {
  searchParams: { message?: string };
}) {
  const user = await getCurrentUser();

  if (!user) {
    redirect('/login');
  }

  const posts = await getPostsByAuthorId(user.id);

  const isSuccessMessage = searchParams.message?.includes('success');

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-3xl font-bold font-headline">My Posts</h1>
        {posts.length > 0 && ( // Only show button if there are posts
            <Button asChild>
            <Link href="/create-post">
                <PlusCircle className="mr-2 h-4 w-4" />
                Create New Post
            </Link>
            </Button>
        )}
      </div>

       {searchParams.message && (
        <Alert variant={isSuccessMessage ? 'success' : 'default'} className="mb-6">
          {isSuccessMessage ? <CheckCircle className="h-4 w-4" /> : <Info className="h-4 w-4" />}
          <AlertTitle>{isSuccessMessage ? 'Success' : 'Notification'}</AlertTitle>
          <AlertDescription>
            {searchParams.message}
          </AlertDescription>
        </Alert>
      )}

      {posts.length > 0 ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {posts.map(post => (
            <MyPostCard key={post.id} post={post} />
          ))}
        </div>
      ) : (
        <Card className="w-full">
            <CardHeader className="text-center">
                <CardTitle className="text-2xl font-bold">Create a New Order Post</CardTitle>
                <CardDescription>Let others in your building know you're ordering.</CardDescription>
            </CardHeader>
            <CardContent className="flex flex-col items-center justify-center p-10">
                 <div className="flex justify-center mb-4">
                    <img src="/images/empty-state.svg" alt="No Posts" className="w-48 h-48 opacity-75"/>
                 </div>
                <Button asChild size="lg" className="mt-6">
                    <Link href="/create-post">
                        <PlusCircle className="mr-2 h-5 w-5" />
                        Start a Group Order
                    </Link>
                </Button>
            </CardContent>
        </Card>
      )}
    </div>
  );
}
